.about{
    @include section;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    overflow: hidden;

    &__item{
        width: 20rem;
        position: relative;
        transition: all .5s;

        &:not(:last-child){
            margin-right: 10rem;
        }

        &:hover{
            transform: translate(1rem, 1rem);
        }

        @include respond(phone){
            width: 90%;
            margin-right: 0 !important;
            margin-bottom: 1rem;
        }
    }

    &__item-overlay{
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        background-image: linear-gradient(to bottom, rgba($white, .2), rgba($white, .8));
    }

    &__item-title{
        text-transform: uppercase;
        font-size: 2rem;
        letter-spacing: .5rem;
        position: absolute;
        top: 10%;
        background-color: $white;
        left: -2rem;
        padding-right: .5rem;
        padding-left: .5rem;
        box-shadow: 0 1rem 3rem rgba($grey, 0.1);
    }

    &__item-pic{
        display: block;
        width: 100%;
        height: 30rem;
        object-fit: cover;

        @include respond(phone){
            height: 10vh;
        }
    }

    &__item-description{
        position: relative;
        margin-top: 1rem;
        font-size: 1.6rem;
        z-index: 10;
    }
}


